               Оформить заявку\n                            </b-button>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </section>\n    </LandingLayout>\n</template>\n\n<script>\n    export default {\n        name: \"TourSearch\",\n        data: () => ({\n            icons:[\"fa fa-comment\",\"fas fa-microphone\",\"fas fa-phone\"],\n            menuUrlList:[\"#contactModalBox\",\"#customVoiceModal\",\"#customPhoneModal\"],\n            titles:[\"Написать сообщение\",\"Записать голосовое\",\"Заказать звонок\"],\n        }),\n        created() {\n            // let SletatRuScript = document.createElement('script')\n            // SletatRuScript.setAttribute('src', 'https://front.sletat.ru/modules/module6/latest/module.js')\n            // SletatRuScript.defer = true;\n            // SletatRuScript.async = true;\n            // document.head.appendChild(SletatRuScript)\n            let tourvisor = document.createElement('script');\n            tourvisor.setAttribute('src', 'https://tourvisor.ru/module/init.js');\n            tourvisor.defer = true;\n            tourvisor.async = true;\n            document.head.appendChild(tourvisor);\n        },\n        computed:{\n            locationByIp() {\n                return this.$store.getters.locationByIp\n            },\n        },\n        methods:{\n            toTourSearch() {\n                this.$router.push('/tour-search')\n            },\n            toAviaSearch() {\n                this.$router.push('/avia-search')\n                // this.$router.push('/avia-module')\n\n            },\n            toHotelSearch() {\n                // this.$router.push('/hotel-search')\n                // this.$router.push('/hotel-module')\n                this.$router.push('/russia-tour-search')\n            },\n        }\n    }\n</script>\n\n<style scoped>\n    .btn-travel {\n        border-radius: 50px;\n        background: #f08b23;\n        color: white;\n        text-transform: uppercase;\n        font-family: \"Open Sans\";\n        font-weight: 900;\n        font-size: 20px;\n        outline: none;\n        border: none;\n    }\n    .footer {\n        position: relative;\n        text-align: center;\n    //background: linear-gradient(60deg, rgba(84, 58, 183, 1) 0%, rgba(0, 172, 193, 1) 100%);\n        color: white;\n        /*height: 65vh;*/\n    }\n    .inner-header {\n        height: 65vh;\n        width: 100%;\n        margin: 0;\n        padding: 0;\n    }\n    .flex {\n        /*Flexbox for containers*/\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        text-align: center;\n    }\n    .waves {\n        position: relative;\n        width: 100%;\n        height: 15vh;\n        margin-bottom: -7px;\n        /*Fix for safari gap*/\n        min-height: 100px;\n        max-height: 150px;\n    }\n    .footer-content {\n        position: relative;\n        height: 35vh;\n        text-align: center;\n        background-color: #ff9900;\n    }\n    .footer-content h1 {\n        /*font-family: 'Bello Pro';*/\n        font-family: 'Open Sans Extrabold';\n    }\n    .inner-content{\n        position: relative;\n        text-align: center;\n        background-color: #062348;\n        width:100%;\n    }\n    .inner-content .waves {\n        position: absolute;\n        top:-75px;\n        height: 12vh;\n        /*Fix for safari gap*/\n        min-height: 70px;\n        max-height: 120px;\n    }\n    .inner-flex{\n        /*Flexbox for containers*/\n        display: flex;\n        justify-content: center;\n        align-items: flex-end;\n        text-align: center;\n    }\n    /* Animation */\n    .parallax>use {\n        animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;\n    }\n    .parallax>use:nth-child(1) {\n        animation-delay: -2s;\n        animation-duration: 7s;\n    }\n    .parallax>use:nth-child(2) {\n        animation-delay: -3s;\n        animation-duration: 10s;\n    }\n    .parallax>use:nth-child(3) {\n        animation-delay: -4s;\n        animation-duration: 13s;\n    }\n    .parallax>use:nth-child(4) {\n        animation-delay: -5s;\n        animation-duration: 20s;\n    }\n    @keyframes move-forever {\n        0% {\n            transform: translate3d(-90px, 0, 0);\n        }\n        100% {\n            transform: translate3d(85px, 0, 0);\n        }\n    }\n    /*Shrinking for mobile*/\n    @media (max-width: 768px) {\n        .waves {\n            height: 50px;\n            min-height: 50px;\n            margin-bottom: 0px;\n        }\n        .footer-content {\n            height: 30vh;\n        }\n        .inner-content .waves {\n            top: -45px;\n            height: 50px;\n            min-height: 50px;\n        }\n        .footer-content h1 {\n            font-size: 27px;\n        }\n    }\n\n    .footer-btn {\n        background-color: #062348;\n        border: none;\n        color: white;\n        font-family: 'Open Sans';\n        font-weight: 900;\n        font-size: 20px;\n        border-radius: 50px;\n        text-transform: uppercase;\n        padding: 10px 32px;\n        text-decoration: none;\n        outline: none !important;\n\n    }\n    .footer-illustration {\n        /*position:absolute;*/\n        /*bottom:0px;*/\n        /*left: 0px;*/\n        width: 100%;\n        /*height: 100%;*/\n        display: block;\n        object-fit: contain;\n    }\n    .more-text {\n        font-family: \"Open Sans\";\n        text-transform: uppercase;\n        color: #f08b23;\n        font-weight: 600;\n        font-size: 16px;\n        cursor:pointer;\n    }\n    .more-button {\n        cursor:pointer;\n        margin: 0 10px;\n        right: 10px;\n    }\n    .more-button div {\n        width: 40px;\n        height: 40px;\n        display: block;\n        border-radius: 50px;\n        text-decoration: none;\n        font-size: 40px;\n        /*line-height: 50px;*/\n        color:  white;\n        background: #f08b23;\n        padding: 0px !important;\n    }\n\n    .footer .more-button div {\n        width: 50px;\n        height: 50px;\n        font-size: 30px;\n        padding-top: 10px !important\n    }\n    .footer .more-text {\n        color: white;\n        font-size: 20px;\n        font-weight: 900;\n        margin-top: auto;\n        margin-bottom: auto;\n    }\n    @media screen and (max-width: 576px) {\n        .tablet-footer-col {\n            -ms-flex: 0 0 55%;\n            flex: 0 0 55%;\n            max-width: 55%;\n        }\n    }\n    @media screen and (max-width: 992px) {\n        .tablet-footer-col {\n            -ms-flex: 0 0 50% ;\n            flex: 0 0 50% ;\n            max-width: 50%;\n        }\n    }\n    @media screen and (max-width: 477px) {\n        .tablet-footer-col {\n            -ms-flex: 0 0 100% !important;\n            flex: 0 0 100% !important;\n            max-width: 100% !important;\n        }\n    }\n\n</style>\n"],"sourceRoot":""}]);
